import React from 'react'

export default class Table extends React.Component {
    render() {
        return (
                <table className="table">
                    <thead>
                        <tr>
                            <th>User ID</th>
                            <th>Registration</th>
                            <th>Last Activity</th>
                        </tr>
                    </thead>

                    <tbody>{
                        this.props.data.map(activity => { 
                            return (
                                <tr id={activity.userID} key={activity.userID}>
                                    <td>{activity.userID}</td>
                                    <td>{activity.dateRegistration}</td>
                                    <td>{activity.dateLastActivity}</td>
                                    {/* <td><button className="remove-btn">Remove</button></td> */}
                                </tr>

                        )})
                    }
                    </tbody>
                </table>
        )
    }
}